/* for the rest of the cinnamon roll images on the web page */
.croll-img {
    width: 260px;
    height: 260px;
    border: 2px solid;
}
/* creates grid-like structure for placement of each cinnamon roll on the web page */
#croll-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    justify-content: space-between;
    row-gap: 100px;
    column-gap: 20px;
    margin-left: 50px;
    margin-right: 50px;
}
/* creates the width and margins for each cinnamon roll placement */
.crolls {
    margin-left: auto;
    margin-right: auto;
    width: 260px;
}
/* creates spacing between the divs */
.glazing, .pack-size, .cart, #header {
    display: flex;
    justify-content: space-between;
}
/* adds spacing between the pack size buttons, the Add to Cart button, and the dropdown menu */
.pack-buttons {
    width: 150px;
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    margin-bottom: auto;
}
/* creates the look for the buttons and dropdown for each cinnamon roll */
.btn, .btn-pack, .dropdown {
    border: 2px black solid;
    background-color: white;
    height: 30px;
}
/* creates width and height of pack size buttons */
.btn-pack {
    width: 34px;
    height: 34px;
}
/* this is what creates the light gray background whenever the pack size buttons are clicked on */
/* .btn-pack:focus {
    background-color: lightgray;
} */
/* creates bold text for whatever text on the webpage needs it */
.bold-txt {
    font-weight: bold;
}
/* turns all text to Helvetica font */
.txt {
    font-family: Helvetica;
}
/* creates yellow background whenever a user hovers over the Add to Cart button */
.add-btn:hover {
    background-color: yellow;
}
/* gives text a bigger font size */
.bigger {
    font-size: 16px;
}
/* gives text a smaller font size */
.smaller {
    font-size: 14px;
}
/* gives button specific width and margin */
.single-btn {
    width: 150px;
    margin-top: auto;
    margin-bottom: auto;
}
/* gives Add to Cart button a specific look */
.add-btn {
    border: 2px black solid;
    background-color: white;
    height: 30px;
    width: 150px;
    margin-top: auto;
    margin-bottom: auto;
}

/* styles the popup that shows up when clicking on add to cart buttons */
.costPopup {
    /* display: block; */
    position: fixed;
    top: 8%;
    bottom: 90%;
    right: 3%;
    border: 1px black solid;
    background-color: white;
    width: 170px;
    height: 100px;
    z-index: 9999;
}

/* styles the text that appears in popup */
.orderContent {
    background-color: white;
    text-align: left;
    font-family: Helvetica;
    font-size: 12px;
}
/* removes margins between lines of text in popup except for first line of text in popup */
.content {
    margin-top: 0px;
    margin-bottom: 0px;
}
/* removes top margin from first line of text in popup */
.addLine {
    margin-top: 0px;
}
/* makes text that contains the type of cinnamon roll in the popup bold */
.typeLine {
    font-weight: bold;
}